import { Component, OnInit, EventEmitter } from '@angular/core';

@Component({
  moduleId: module.id,
  selector: 'product-filter',
  outputs: ['filterChange'],
  template: `
      <section>
        <h3>Filter</h3>
        By Name:  <input type="text" [(ngModel)]="filter.byName" (input)="filterChanged()" />
        By Price: <input type="number" [(ngModel)]="filter.byPrice" (input)="filterChanged()" />
      </section>

  `
})
export class ProductFilterComponent implements OnInit {
    
  private filterChange = new EventEmitter();
  private filter = {byName: '', byPrice: ''};
  
  constructor() { }

  ngOnInit() { }
  filterChanged() {
    this.filterChange.emit(this.filter);
  }

}